@import '../../../styles/variables';
@import '../../../styles/btn';

.gallery__container {
    padding: 100px 0;

    .gallery__container_filters {
        text-align: center;
        font-size: $size;
        margin: 40px auto;

        form {
            display: flex;
            flex-direction: column;

            div {
                margin-top: 20px;
                display: flex;
                justify-content: center;
                gap: 10px;
                flex-wrap: wrap;

                input {
                    display: none;
                }

                label {
                    padding: 4px 30px;
                    border: 1px solid $secondary-color;
                    border-radius: 3px;
                    color: $secondary-color;
                    cursor: pointer;
                }
        
                input:checked + label {
                    background: $secondary-color;
                    color: $primary-color;
                }
            }
        }
    }

    .gallery__container_wrapper {
        display: flex;
        flex-wrap: wrap;
        gap: 10px;
        max-width: 930px;
        justify-content: center;
        margin: 0 auto;

        img {
            width: 300px;
            height: 200px;
            cursor: pointer;
            border-radius: 8px;
            transition: $trns;
            object-fit: cover;

            &:hover {
                transform: scale(1.05);
                opacity: 0.9;
                box-shadow: 4px 4px 5px rgba(0,0,0,.4);
            }
        }
    }

    .gallery__container_carousel {
        position: relative;
        max-width: 900px;
        margin: 0 auto;

        &>button {
            background: rgba(0,0,0,0.4);
            position: absolute;
            top: 0;
            right: 0;
            border-top: 2px solid transparent;
            z-index: 10;
            font-size: 30px;
            padding: 5px 10px;
            height: 33px;
            color: $primary-color;
            border: none;
            cursor: pointer;
            line-height: 34px;

            span {
                position: relative;
                top: -9px;
                left: -3px;
            }
        }
    }
}
.btn_show {
    display: block;
}

@media screen and (max-width: $medium) {
    .gallery__container {
        padding: 70px 0;
        .gallery__container_wrapper {
            img {
                width: calc(100% - 20px);
                height:fit-content;
            }
        }
    }
    

    .btn_show {
        display: none !important;
    }

    .gallery__display {
        display: none !important;
    }

    .carousel_display {
        display: block !important;
    }
}