@import '../../../styles/variables';
@import '../../../styles/mixins';

.contacts__container {
    background: url($bg);
    // padding: 100px;
    box-shadow: 0 -2px 10px rgba(0,0,0, .2);
    overflow: hidden;
    z-index: 0;
    position: relative;

    &::after {
        @include euqaliptus_imgs;
        top: 0;
        left: 0;
        background-image: url('../../../media/eq2-rev.png');
    }

    &::before {
        @include euqaliptus_imgs;
        bottom: 0;
        right: 0;
        background-image: url('../../../media/eq2.png');
        transform: rotate(180deg);
    }
    
    .contacts__container_wrapper {
        // padding: 100px 100px 150px 100px;
        text-align: center;
        width: 100vw;
        max-width: 1100px;
        padding: 180px 0 250px;
        margin: 0 auto;

        .contacts__container_inner {
            position: relative;

            .contacts__box {
                width: 70%;
                margin: 0 auto;

                p {
                    position: relative;
                    z-index: 10;
                    margin: 10px auto;
        
                    &:nth-of-type(1) {
                        font-size: 56px;
                        
                    }
                    &:nth-of-type(2),
                    &:nth-of-type(3) {
                        font-size: 40px; 
                    }
                    a {
                        text-decoration: none;
                        color: $secondary-color;
                    } 
                }

                
            }

            img {
                position: absolute;
                top: -260px;
                left: 49%;
                transform: translateX(-50%);
                width: 100%;
                z-index: 1;
                height: 704px;
                max-width: 1000px;
            }
        }
    }
}

@media screen and (max-width: $large) {
    .contacts__container {
        .contacts__container_wrapper { 
            p {

                &:nth-of-type(1) {
                    font-size: 40px;
                }
            }
        }
    }
}

@media screen and (max-width: $medium) {
    .contacts__container {
        .contacts__container_wrapper { 
            padding-bottom: 180px;
            p {
                font-size: 36px !important;
            }

            .contacts__container_inner {
                img {
                    top: -230px;
                    height: 600px;
                }
            }
        }
    }
}

@media screen and (max-width: 545px) {
    .contacts__container {
        &::after,
        &::before {
            display: block;
        }
        .contacts__container_wrapper { 
            padding: 40px 0;
            .contacts__container_inner {
                img {
                    display: none;
                }
            }
        }
    }
}

@media screen and (max-width: $small) {
    .contacts__container {
        .contacts__container_wrapper {
            p {
                &:nth-of-type(1),
                &:nth-of-type(2),
                &:nth-of-type(3) {
                    font-size: 30px !important;
                }
            }
        }
    }
}



