@import '../../../styles/variables';

.slider {
    width: 100%;
    height: 100vh;
    background: url('../../../media/slide-1.jpg') no-repeat;
    background-position: center;
    background-size: cover;
    transition: all 2.5s ease;
    object-fit: cover;
}

@media screen and (max-width: $medium) {
    .slider {
        height: 85vh;
    } 
} 
