@mixin pallete-wide {
    flex: 2;
    max-width: 600px;
    max-height: 200px;
    object-fit: cover;
}

@mixin pallete {
    flex: 1;
    object-fit: cover;
}

@mixin creative_img {
    flex: 1;
    max-width: 80%;
}

@mixin euqaliptus_imgs {
    content: '';
    position: absolute;
    height: 200px;
    width: 200px;
    background-size: contain;
    background-repeat: no-repeat;
    z-index: 1;
    display: none;
}