@import '../../../styles/variables';
@import '../../../styles/h4';

.about__container {
    margin: 0 auto;
    padding: 100px 0;
    text-align: center;
    background-image: url($bg);

    .about__container_services {
        margin: 0 auto;

        p {
            font-size: 30px;
        }

        .about__weeding, 
        .about__event,
        .about__buffet, 
        .about__sweet {
            display: flex;
            justify-content: center;
            gap: 40px;
            margin-bottom: 40px;

            p, h2 {
                font-size: 30px; 
                line-height: 600px;
            
            }

            h2 {
                font-weight: normal;
            }

            img {
                border-radius: 100%;
                object-fit: cover;
                width: 600px;
                height: 600px;
                box-shadow: 0 0 10px rgba(0,0,0,.9);
            }
        }

        .about__qr {
            width: 200px;
        }
        
        .about__bestImg {
            width: 700px;
            border-radius: 4px;
        }

        
    }
}

.small_hide {
    display: block;
}

.small_show {
    display: none;
}

@media screen and (max-width: $large) {
    .about__weeding, 
    .about__event,
    .about__buffet, 
    .about__sweet {
        flex-direction: column;
        align-items: center;

        h2 {
            line-height: 30px !important;
        }
    }

    .small_hide {
        display: none;
    }
    .small_show {
        display: block;
    }
}

@media screen and (max-width: $medium) {
    .about__container {
        padding: 70px 0;
        .about__container_services {
            .about__weeding, 
            .about__event,
            .about__buffet, 
            .about__sweet {
                img {
                    width: 60% !important;
                    height: 60vw !important;
                }
            }
            .about__bestImg {
                width: calc(100% - 20px) !important;
            }
        } 
    }
    
}