@import '../../../styles/variables';
@import '../../../styles/mixins';

.creative__container {
    padding: 100px 0;

    .creative__container_inner {
        margin: 0 auto;
        max-width: 900px;

        .creative__container_wrapper {
            display: flex;
            flex-direction: column;
            margin: 0 auto;

            .creative__container_row {
                display: flex;
                justify-content: center;
                height: 200px;
                max-width: 900px;
                flex-wrap: wrap;

                img {
                    cursor: pointer;
                    max-width: 300px;
                    transition: $trns;

                    &:hover {
                        opacity: 0.7;
                    }
                }

                &:nth-of-type(1) {
                    img {
                        &:nth-of-type(1) {
                            @include pallete-wide;
                        }
                        &:nth-of-type(2) {
                            @include pallete;
                        }
                    }
                }

                &:nth-of-type(3) {
                    img {
                        &:nth-of-type(1) {
                            @include pallete;
                        }
                        &:nth-of-type(2) {
                            @include pallete-wide;
                        }
                    }
                }  
            }
        }
    }
}

@media screen and (max-width: $large) {
    .creative__container {
        .creative__container_inner {
            .creative__container_wrapper {
                .creative__container_row {
                    &:nth-of-type(2) {
                        flex-wrap: nowrap;
                        img {
                            max-width: 33.33%;
                        }
                    }
                }
            }
        }
    }
}

@media screen and (max-width: $medium) {
    .creative__container {
        padding: 70px 0;
        .creative__container_inner {
            .creative__container_wrapper {
                .creative__container_row {
                        flex-wrap: wrap;
                        height: auto;
    
                    img {
                        margin: 6px auto;
                        max-width: 80%;
                    }
    
                    &:nth-of-type(1) {
                        img {
                            &:nth-of-type(1) {    
                                @include creative_img;
                                max-height:fit-content;
                            }
                            &:nth-of-type(2) {
                                @include creative_img;
                            }
                        }
                    }
    
                    &:nth-of-type(2) {
                        flex-wrap: wrap;
                        img {
                            max-width: 80%;
                        }
                    }
    
                    &:nth-of-type(3) {
                        img {
                            &:nth-of-type(1) {
                                @include creative_img;
                            }
                            &:nth-of-type(2) {   
                                @include creative_img;
                                max-height:fit-content;
                            }
                        }
                    }  
                }
            }
        }
    }
}



