@import '../../../styles/variables';

header {
    position: sticky;
    top: 0;
    z-index: 1000;
    background-image: url($bg);
    opacity: 0.9;
    box-shadow: 0 2px 5px rgba(0,0,0,.4);

    div {
        display: flex;
        flex-direction: row;
        justify-content: center;

        .header__container_logo, 
        a {
            color: $secondary-color;
        }

        .header__container_logo {
            font-size: 28px;
            font-style: italic;
            margin-right: 100px;
            padding: 12px;
            cursor: pointer;
            height: 70px;

            img {
                height: 40px;
                position: relative;
                top: 4px;
            }
        }
        ul {
            list-style: none;
            display: flex;
            justify-content: center;

        }

        ul li,
        a {
            transition: $trns;
        }

        a {
            text-decoration: none;
            font-size: $size;
            padding: 20px 30px;
            display: block;
        }

        li:hover {
            cursor: pointer;
            background: $secondary-color;
        }
        li:hover > a {
            color: $primary-color;
        }
    }
}
.header__menu {
    display: none;
}

.menu__btn {
    display: none;
}

@media screen and (max-width: $large) {
    header {
        div {
            align-items: center;
            flex-direction: column;
    
            .header__container_logo {
                margin-right: 0;
            }

            ul { 
                display: none;
            }
        } 
    }
            
    .header__menu {
        transition: all .3s ease;
        display: block;
        position: absolute;
        left: 0;
        max-width: 100%;
        background-image: url($bg);

        li {
            text-align: center;

            a {
                display: block;
                width: 100vw;
                max-width: 100%;
            }
        }
    }

    .menu__btn {
        display: block;
        position: absolute;
        top: 20px;
        right: 15px;
        width: 30px;
        height: 30px;
        background: rgba(176, 91, 59,.8);
        z-index: 1001;
        border-radius: 100%;
        border: none;

        span {
            display: block;
            position: absolute;
            top: 7px;
            right: 7px;
            width: 16px;
            height: 16px;
            background: #fff;
            z-index: 1002;
            border-radius: 100%;
        }
    }
}




