@import 'variables';

.btn {
    border: 1px solid $secondary-color;
    padding: 6px 96px;
    display: block;
    margin: 20px auto;
    background: none;
    border-radius: 4px;
    color: $secondary-color;
    font-size: $size;
    cursor: pointer;
    transition: all .3s ease;

    &:hover {
        background: $secondary-color;
        color: $primary-color;
    }
}